import React, { useState, useEffect } from "react";
import { Table, Tag, Space } from "antd";
import { FormContext } from "antd/lib/form/context";
import axios from "axios";

import { Route, Switch, Redirect,Link } from "react-router-dom";

const columns = [
  {
    title: "Id",
    dataIndex: "_id",
    key: "id",
    render: (text) => <Link to={`/collegeDetail?${text}`} >{text}</Link>,
  },
  {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
  {
    title: "Year",
    dataIndex: "year",
    key: "Year",
  },
  {
    title: "city",
    dataIndex: "city",
    key: "city",
  },
  {
    title: "state",
    dataIndex: "state",
    key: "state",
  },
  {
    title: "country",
    dataIndex: "country",
    key: "country",
  },
  {
    title: "students",
    dataIndex: "count",
    key: "students",
  },
  {
    title: "courses",
    key: "courses",
    dataIndex: "courses",
    render: (courses) => (
      <>
        {courses.map((tag) => {
          let color = "blue";
          return (
            <Tag color={color} key={tag}>
              {tag.toUpperCase()}
            </Tag>
          );
        })}
      </>
    ),
  },
];

const scol =[
  {
      title: "Id",
      dataIndex: "_id",
      key: "_id",
      render: (text) => <a>{text}</a>,
    },
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "College",
      dataIndex: "college",
      key: "college",
    },
    {
      title: "Year",
      dataIndex: "year",
      key: "year",
    },
    {
      title: "Skills",
      key: "skills",
      dataIndex: "skills",
      render: (skills) => (
        <>
          {skills.map((tag) => {
            let color = "blue";
            return (
              <Tag color={color} key={tag}>
                {tag.toUpperCase()}
              </Tag>
            );
          })}
        </>
      ),
    },
  ]

const College = () => {
  const [docs, setdocs] = useState([]);
  useEffect(() => {
    async function fetch() {
      const doc = await axios.get("http://localhost:8000/college/all");
      console.log(doc.data.data.data);
      setdocs([...doc.data.data.data]);
    }
    fetch();
    console.log(docs);
  }, []);
  return <Table columns={columns} dataSource={docs} />;
};

export default College;
