import React, { useState, useEffect } from "react";
import { Route, Switch, Redirect, Link } from "react-router-dom";
import { Layout, Menu } from "antd";

import "./App.css";
import College from "./colleges";
import DashCourse from "./dashCourse";
import DashState from "./dashState";
import CollegeDetail from "./collegeDetail";
import StudentDetail from "./studentDetail";
//import { UploadOutlined, UserOutlined, VideoCameraOutlined } from '@ant-design/icons';

const { Header, Content, Footer, Sider } = Layout;

const App = () => {
  return (
    <Layout>
      <Sider
        breakpoint="lg"
        collapsedWidth="0"
        onBreakpoint={(broken) => {
          console.log(broken);
        }}
        onCollapse={(collapsed, type) => {
          console.log(collapsed, type);
        }}
      >
        <div className="logo" />
        <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]} >
          <Menu.Item key="1" >
            <Link to="/" >All Colleges</Link>
          </Menu.Item>
          <Menu.Item key="2" >
            <Link to="/dashCourse" >Dash Course</Link>
          </Menu.Item>
          <Menu.Item key="3" >
            <Link to="/dashState" >Dash State</Link>
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout style={{ minHeight: "100vh" }}>
        <Header
          className="site-layout-sub-header-background"
          style={{ padding: 0 }}
        >
          <h1 style={{textAlign:"center",fontSize: "2rem"}}>Colleges in India</h1>
        </Header>
        <Content style={{ margin: "24px 16px 0"}}>
          <div
            className="site-layout-background"
            style={{ padding: 24, minHeight: 360,fontSize: "1.2rem"  }}
          >
            <Switch>
              <Route exact path="/" render={(props) => <College />} />
              <Route
                exact
                path="/dashCourse"
                render={(props) => <DashCourse />}
              />
              <Route
                exact
                path="/dashState"
                render={(props) => <DashState />}
              />
              <Route
                exact
                path="/collegeDetail"
                render={(props) => <CollegeDetail fn={props} />}
              />
              <Route
                exact
                path="/studentDetail"
                render={(props) => <StudentDetail fn={props} />}
              />
            </Switch>
          </div>
        </Content>
      </Layout>
    </Layout>
  );
};

export default App;
