import React, { useState, useEffect } from "react";
import "./App.css";
import { Divider } from 'antd';
import { Table, Tag, Space } from "antd";
import { Link } from "react-router-dom";
import axios from 'axios';

const columns = [
    {
      title: "Id",
      dataIndex: "_id",
      key: "id",
      render: (text) => <Link to={`/collegeDetail?${text}`} >{text}</Link>,
    },
    {
        title: "Name",
        dataIndex: "name",
        key: "name",
      },
    {
      title: "Year",
      dataIndex: "year",
      key: "Year",
    },
    {
      title: "city",
      dataIndex: "city",
      key: "city",
    },
    {
      title: "state",
      dataIndex: "state",
      key: "state",
    },
    {
      title: "country",
      dataIndex: "country",
      key: "country",
    },
    {
      title: "students",
      dataIndex: "count",
      key: "students",
    },
    {
      title: "courses",
      key: "courses",
      dataIndex: "courses",
      render: (courses) => (
        <>
          {courses.map((tag) => {
            let color = "blue";
            return (
              <Tag color={color} key={tag}>
                {tag.toUpperCase()}
              </Tag>
            );
          })}
        </>
      ),
    },
  ];

const scol =[
    {
        title: "Id",
        dataIndex: "_id",
        key: "_id",
        render: (text) => <Link to={`/studentDetail?${text}`} >{text}</Link>,
      },
      {
        title: "Name",
        dataIndex: "name",
        key: "name",
      },
      
      {
        title: "Year",
        dataIndex: "year",
        key: "year",
      },
      {
        title: "Skills",
        key: "skills",
        dataIndex: "skills",
        render: (skills) => (
          <>
            {skills.map((tag) => {
              let color = "blue";
              return (
                <Tag color={color} key={tag}>
                  {tag.toUpperCase()}
                </Tag>
              );
            })}
          </>
        ),
      },
    ]

const CollegeDetail = (props)=>{
    console.log(props.fn.location.search.substr(1));
    const [col,setcol] = useState([]);
    const [stu,setstu] = useState([]);
    const [sc,setsc] = useState([]);
    useEffect(()=>{
        console.log('hello');
        async function fetch() {
            const doc = await axios.post("http://localhost:8000/college",{id:props.fn.location.search.substr(1)});
            console.log(doc.data.data.doc);
            setcol(doc.data.data.doc);
            const doc1 = await axios.post("http://localhost:8000/student/college",{id:props.fn.location.search.substr(1)});
            setstu(doc1.data.data.ress);
            console.log(doc1.data.data);
            const doc2 = await axios.post("http://localhost:8000/college/similar",{id:props.fn.location.search.substr(1)});
            console.log(doc2.data.data.result);
            setsc(doc2.data.data.result);
          }
          fetch();
    },[])
    return (
        <>
        <div>
        <h1>College Details</h1>
        <div><span className='wid'>Id</span> : {col._id}</div>
        <div><span className='wid'>Name</span>: {col.name}</div>
        <div><span className='wid'>Year</span>: {col.year}</div>
        <div><span className='wid'>City</span>: {col.city}</div>
        <div><span className='wid'>State</span>: {col.state}</div>
        <div><span className='wid'>Country</span>: {col.country}</div>
        <div><span className='wid'>Count</span>: {col.count}</div>
        <div><span className='wid'>Courses</span>: {col.courses && col.courses.join()}</div>
        </div>
        <Divider />
        <h1>Students in {col.name}</h1>
         <Table columns={scol} dataSource={stu}/>
         <Divider />
         <h1>Similar colleges of {col.name}</h1>
         <Table columns={columns} dataSource={sc}/>
        </>

    )
}

export default CollegeDetail;