import React, { useState, useEffect } from "react";
import axios from 'axios';

const StudentDetail = (props)=>{
    console.log(props.fn.location.search.substr(1));
    const [col,setcol] = useState([]);
    useEffect(()=>{
        console.log('hello');
        async function fetch() {
            const doc = await axios.post("http://localhost:8000/student/all",{id:props.fn.location.search.substr(1)});
            console.log(doc.data.data.doc);
            setcol(doc.data.data.doc)
        }
        fetch();
    },[]);
    return(
        <>
        <div>
        <h1>Student Details</h1>
        <div><span className='wid'>Id</span> : {col._id}</div>
        <div><span className='wid'>Name</span> : {col.name}</div>
        <div><span className='wid'>year</span> : {col.year}</div>
        <div><span className='wid'>college</span> : {col.college && col.college.name}</div>
        <div><span className='wid'>skills</span> : {col.skills && col.skills.join()}</div>
        </div>
        </>
    )
}

export default StudentDetail;