import React, { useState, useEffect } from "react";
import {CanvasJSChart} from 'canvasjs-react-charts';
import { Route, Switch, Redirect,Link } from "react-router-dom";
import { Table, Tag, Space } from "antd";
import { Divider } from 'antd';
import axios from 'axios';

const columns = [
  {
    title: "Id",
    dataIndex: "_id",
    key: "id",
    render: (text) => <Link to={`/collegeDetail?${text}`} >{text}</Link>,
  },
  {
    title: "Name",
    dataIndex: "name",
    key: "name",
  },
  {
    title: "Year",
    dataIndex: "year",
    key: "Year",
  },
  {
    title: "city",
    dataIndex: "city",
    key: "city",
  },
  {
    title: "state",
    dataIndex: "state",
    key: "state",
  },
  {
    title: "country",
    dataIndex: "country",
    key: "country",
  },
  {
    title: "students",
    dataIndex: "count",
    key: "students",
  },
  {
    title: "courses",
    key: "courses",
    dataIndex: "courses",
    render: (courses) => (
      <>
        {courses.map((tag) => {
          let color = "blue";
          return (
            <Tag color={color} key={tag}>
              {tag.toUpperCase()}
            </Tag>
          );
        })}
      </>
    ),
  },
];

const DashCourse = ()=>{
    const [sim,setsim] = useState([]);
    const [cours,setCourse] = useState([]);
    const [name,setname] = useState('');
    useEffect(()=>{
        async function fetch() {
            const doc = await axios.get("http://localhost:8000/college/courses");
            console.log(doc.data.data.ans);
            setsim(doc.data.data.ans);
          }
          fetch();
          console.log(sim);
    },[])
    const options = {
        
        exportEnabled: true,
        animationEnabled: true,
        title: {
            text: "Dashboard by Course"
        },
        data: [{
            click: async function(e){
                console.log(e.dataPoint.label);
                const doc = await axios.post("http://localhost:8000/college/acourses",{course:e.dataPoint.label});
                console.log(doc.data.data);
                setCourse(doc.data.data.ress);
                setname(e.dataPoint.label+' offered colleges')
            },
            explodeOnClick: false,
            type: "doughnut",
            startAngle: 75,
            toolTipContent: "<b>{label}</b>: {y}%",
            showInLegend: true,
            legendText: "{label}",
            indexLabelFontSize: 16,
            indexLabel: "{label} - {y}%",
            dataPoints: sim
        }]
    }
    return (
        <>
        <CanvasJSChart options={options}/>
        <Divider/>
        {name}
        
        <Table columns={columns} dataSource={cours}/>
        </>
    )
}

export default DashCourse;